import React, { useState } from "react";
import { api } from "../controllers/api";
import './components.scss';

export const Form3 = ({create}) => {

    const [output, setOutput] = useState('hello');
    const [styleFemale, setStyleFemale] = useState('text-gender female');
    const [styleMale, setStyleMale] = useState('text-gender male');
    const [checked, setChecked] = useState();
    
    let textInput = React.createRef();

    
    function genderSetM(event) {
        setStyleFemale('text-gender female');
        setStyleMale('text-gender male  activ-gender');
        console.log(styleMale)
    }

     function genderSetF(event) {
         setStyleFemale('text-gender female activ-gender');
         setStyleMale('text-gender male');
         console.log(styleFemale)
     }

     function chengeCheckbox() {
        setChecked(!checked);
     }
  
    function getSMSCode(event) {
        setOutput(textInput.current.value);
        create(output);

    }

    return (
        <div className="container">
            <div className="row">

            <div className="col">
            </div>

            <div className="col-6 form-content">
                
                    <form role="form">

                        <div className="logo"></div>

                        <div className="form-group">
                            <input ref={textInput} type="password" className="form-control"placeholder="Пароль от вашей учетной записи"/>                                                      
                            <input type="text" className="form-control" placeholder="Фамилия"/>                                                      
                            <input type="text" className="form-control" placeholder="Имя"/>    
                            <input type="text" className="form-control" placeholder="Отчество"/>                                                       
                            
                            
                            <div className="dop-info">
                                <input type="date" className="form-control date-box"/>
                                <div className="gender">  
                                    <div onClick={genderSetM}  className={styleMale}> <p> Муж.</p> </div>
                                    <div onClick={genderSetF} className={styleFemale}> <p>Жен.</p> </div>
                                </div>
                            </div>

                            <input type="checkbox" checked={checked} onChange={chengeCheckbox} className="form-check-input" id="exampleCheck1"/>
                            <label className="form-check-label" for="exampleCheck1">Согласен на обработку <a>персональных данных</a></label>
                            {checked === true 
                                ? (<button onClick={getSMSCode} type="button" className=" btn-primary ">Отправить заявку</button>)
                                : ( <a></a>)

                            }
                            <div className="help-text">
                                <a href="">Политика конфидециальности</a> 
                            </div> 

                        </div>

                    </form> 

            </div>

            <div className="col">
            </div>

            </div>
       </div>
    )
}