import React, { useState } from "react";
import { api } from "../controllers/api";
import './components.scss';

export const Form = ({create}) => {

    const [output, setOutput] = useState('hello');
    let textInput = React.createRef();
    function getSMSCode(event) {
        setOutput(textInput.current.value);
        create(output);
    }

    return (
        <div className="container">
            <div className="row">

            <div className="col">
            </div>

            <div className="col-6 form-content">
                
                    <form role="form">

                        <div className="logo"></div>
                        <div className="form-group">
                            <input ref={textInput} type="email" className="form-control" id="email" placeholder="Введите ваш номер"/>                                                      
                            <button onClick={getSMSCode} type="button" className=" btn-primary ">Отправить смс на номер</button>     
                            
                            <div className="help-text">
                                <a href="">Политика конфидециальности</a> 
                            </div> 

                        </div>

                    </form> 

            </div>

            <div className="col">
            </div>

            </div>
       </div>
    )
}